.page {
    max-width: 360px;
    min-height: 100vh;
    margin: auto;
    background-color: #FFFFFF;
    position: relative;
}
.main__quote{
    max-width: 262px;
    margin: 0 15px 0 83px;
    font-family: "Spectral", serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 27px;
    color: #000000;
    text-align: left;
}
.main__add-request{
    width: 56px;
    height: 56px;
    border-radius: 50%;
    border: 1px solid rgba(207, 75, 75, 1);
    position: absolute;
    bottom: 24px;
    right: 24px;
    z-index: 5;
    background-image: url(/hackathon-2025/static/media/add-request-button.91604cf1.svg);
    background-repeat: no-repeat;
    background-position: center;
}

.main__add-request:hover {
    background-color: #F6F6F6;
}
.main{
    width: 100%;
    color:#42424F;
    position: relative;
}
.header__logo{
    width: 169px;
    height: 20px;
    margin: 0;
    padding: 0;
    cursor: pointer;
}
.header__menu {
    padding: 0;
    width: 32px;
    height: 32px;
    margin: 0;
    cursor: pointer;
}

.header {
    max-width: 100%;
    min-height: 56px;
    display: flex;
    background-color: #000;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
}

.logo{
    background-image: url(/hackathon-2025/static/media/logo.629e7f50.svg);
}
.menu{
    background-image: url(/hackathon-2025/static/media/menu.5f7e23ff.svg);
}
.request-filter__location {
    color: #2F80ED;
    text-decoration: underline;
    cursor: pointer;
}

.request-filter__location:hover {
    color: rgba(189, 28, 28, 1);
}
.request-filter {
    margin: 43px 0 12px 0;
    padding: 0;
    max-width: 100%;
    font-family: "Cera Pro", "Helvetica Neue", "Arial", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 40px;
    color:#42424F;
}
.card__title{
    font-family: "Spectral", serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #42424F;
    margin: 16px 0 0 0;
    padding: 0;
    text-transform: uppercase;
}
.card__address{
    font-family: "Spectral", "serif";
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    margin: 16px 0 0 0;
    padding: 0;
}
.card__image{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin: 24px 0 24px 0;
    background-image: url(/hackathon-2025/static/media/pic.1f49ede9.jpg)
}
.card__poem{
    font-family: "Spectral", "serif";
    font-style: italic;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    border-bottom: 1px solid #F1F1F1;
    margin: 0 0 20px 0;
    padding: 0;
}
.card__footer{
    display: flex;
    margin: 6px auto 0;
    padding: 0;
    align-items: center;
    justify-content: space-between;
}
.card__status{
    width: 107px;
    height: 27px;
    border: 1px solid #000000;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.card__status-text{
    font-family: "Spectral", serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 20px;
    color: #000000;
    text-transform: uppercase;
    margin: 0;
    padding: 0;
}
.card__likes {
    height: 32px;
    width: 52px;
    padding: 4px;
    background-color: #F7F8FC;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}
.card__like-ico{
    background-image: url(/hackathon-2025/static/media/like.d6004b87.svg);
    width: 16px;
    height: 20px;
    margin: 0 4px 0 0 ;
    background-repeat: no-repeat;
    background-size: 16px;
}
.card__like{
    margin: 0;
    padding: 0;
    font-family: 'Inter', 'Helvetica Neue', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #42424F;
}
.card{
    max-width: 330px;
    padding: 16px;
    box-sizing: border-box;
    margin: 20px auto;
    border: 1px solid #000000;
    text-align: left;
}
.main-quote{
    width: 290px;
    height: 272px;
    margin: 11px 55px 16px 15px ;
    background-image: url(/hackathon-2025/static/media/quote.b9565b7d.svg);
}
.address-screen__form {
    display: flex;
    flex-direction: column;
    width: 307px;
    max-height: 228px;
    margin: 0;
    padding: 20px;
    background-color: white;
    border-radius: 20px;
    position: relative;
    z-index: 4;
}
.address-screen__input {
    height: 36px;
    width: 290px;
    border: none;
    border-bottom: 1px solid rgba(148, 148, 148, 1);
    margin: 0 0 20px 0;
    padding: 0 0 12px 0;
    box-sizing: border-box;
    font-family: "Spectral", serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #949494;
    outline: none;
}
.address-screen__close-button {
    position: absolute;
    right: -20px;
    top: -20px;
    width: 20px;
    height: 20px;
    cursor: pointer;
    margin: 0;
    padding: 0;
}
.address-screen__submit-button {
    height: 56px;
    width: 320px;
    border: 1px solid #0F0E0E;
    cursor: pointer;
    font-family: "Montserrat", "Helvetica", "Helvetica", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: #0F0E0E;
}
.address-screen__submit-button:hover {
    color: rgba(148, 148, 148, 1);
    border-color: rgba(148, 148, 148, 1);
}



.address-screen__title {
    text-transform: uppercase;
    margin: 0 0 32px 0;
    padding: 0;
    text-align: left;
    font-family: "Montserrat", "Helvetica", "Helvetica", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 32px;
}
.address-screen {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 100%;
    max-width: 100%;
    height: 100vh;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 0;
    left: 0;
}
.request-form__address{
    width: 320px;
    margin: 0 auto  ;
    min-height: 92px;
    background-color: #2f80ed;

}
.request-form__title{
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 32px;
    /* identical to box height, or 145% */

    text-transform: uppercase;

    /* Primary text */

    color: #0F0E0E;
text-align: left;
    margin: 26px 0 20px 20px;
}
.request-form__input{
    height: 36px;
    width: 290px;
    border: none;
    background-color: rgba(246, 246, 246, 1);
    border-bottom: 1px solid rgba(148, 148, 148, 1);
    margin: 0 0 20px 0;
    padding: 0 5px 12px 5px;
    font-family: "Spectral", serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color:  rgba(15, 14, 14, 1);
    outline: none;
}
.request-form__radio-item{
    height: 20px;
    width: 20px;
    margin: 0;
    padding: 0;
}

.request-form__radio-container{
    list-style: none;
    margin: 0 auto 34px;
    padding: 0;
    width: 100%;
}
.request-form__radio-container-item {
    width: 100%;
    height: 58px;
    margin: 0;
    padding: 18px 20px;
    display: flex;
    box-sizing: border-box;
    flex-direction: row;
    list-style: none;
    align-items: center;
    justify-content: space-between;
    font-family: 'Spectral', serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #0F0E0E;
    cursor: pointer;
}

.request-form__radio-container-item:hover {
    background-color: rgba(246, 246, 246, 1);

}

.request-form__submit-button {
    height: 56px;
    width: 320px;
    border: 1px solid #0F0E0E;
    cursor: pointer;
    font-family: "Montserrat", "Helvetica", "Helvetica", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: #0F0E0E;
    margin: 0 auto 20px;
}
.address-screen__submit-button:hover {
    color: rgba(148, 148, 148, 1);
    border-color: rgba(148, 148, 148, 1);
}




.request-form {
    width: 100%;
    min-height: 100%;
}
.App {
    text-align: center;
    background-color: #282c34;
    transition: all 1s ease-out;
}

