.request-form__radio-container-item {
    width: 100%;
    height: 58px;
    margin: 0;
    padding: 18px 20px;
    display: flex;
    box-sizing: border-box;
    flex-direction: row;
    list-style: none;
    align-items: center;
    justify-content: space-between;
    font-family: 'Spectral', serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #0F0E0E;
    cursor: pointer;
}

.request-form__radio-container-item:hover {
    background-color: rgba(246, 246, 246, 1);

}
